import React from 'react';
import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import useMediaQuery from '@mui/material/useMediaQuery';
import CategoryIcon from '@mui/icons-material/Category';
import { useTheme } from '@mui/material/styles';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import LoadingButton from '@mui/lab/LoadingButton';

import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import DeleteRoundedIcon from '@mui/icons-material/DeleteRounded';
import FolderRoundedIcon from '@mui/icons-material/FolderRounded';

import { getProduct } from '../../../server/Data';
import addLogo from '../../../assets/images/logos_loacles/addLogo.jpg'

import { updateProduct } from '../../../server/UpdateProduct';

//  COMPONENTES UI
// import ShoppingCart from './ShoppingCart';

export default function BorchuresModal() {
    const [open, setOpen] = React.useState(false);
    const [loadBtn, setLoadBtn] = React.useState(false);
    const [panelEl, setPanelEl] = React.useState();

    const theme = useTheme();


    React.useEffect(() => {

        document.addEventListener('open_brochures_modal', function (e) {
            //console.log(e.detail.element.children);
            setPanelEl(e.detail.element)
            setLoadBtn(false)

          
            handleClickOpen();


            let jsonP = JSON.parse(e.detail.element.getAttribute('arrobj'))
            
        })



    }, []);

    





    const BootstrapDialog = styled(Dialog)(({ theme }) => ({
        '& .MuiDialogContent-root': {
            padding: theme.spacing(2),
        },
        '& .MuiDialogActions-root': {
            padding: theme.spacing(1),
        },
    }));

    const BootstrapDialogTitle = (props) => {
        const { children, onClose, ...other } = props;

        return (
            <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
                {children}
                {onClose ? (
                    <IconButton
                        aria-label="close"
                        onClick={onClose}
                        sx={{
                            position: 'absolute',
                            right: 8,
                            top: 8,
                            color: (theme) => theme.palette.grey[500],
                        }}
                    >
                        <CloseIcon />
                    </IconButton>
                ) : null}
            </DialogTitle>
        );
    };

    BootstrapDialogTitle.propTypes = {
        children: PropTypes.node,
        onClose: PropTypes.func.isRequired,
    };

    const ColorButton = styled(Button)(({ theme }) => ({
        color: "white",
        backgroundColor: "#cc293c",
        '&:hover': {
            backgroundColor: "#801925",
        },
    }));

    const contentStyle = {
        minHeight: "10rem",
        width: "100%",
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-around"
    }

    const fieldsContainer = {
        width: "100%",
        display: "flex",
        justifyContent: "space-around"
    }


    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
        setLoadBtn(false)
    };

    const handleSave = () => {
        setOpen(false);
        setLoadBtn(false)

       
    }

    return (
        <div>
            
            <BootstrapDialog
                onClose={handleClose}
                aria-labelledby="customized-dialog-title"
                open={open}
                fullWidth={true}
                maxWidth='sm'
            >
                <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
                    Brochures
                </BootstrapDialogTitle>
                <DialogContent dividers sx={contentStyle}>
                    
                </DialogContent>
                <DialogActions>
                    <ColorButton autoFocus onClick={handleSave} sx={{ backgroundColor: "#cc293c" }}>
                        Cerrar
                    </ColorButton>
                </DialogActions>
            </BootstrapDialog>
        </div>
    );
}