import axios from "axios";

export const getBrochures = (idStand) =>{

  console.log(idStand)

  const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"
  
  const body ={
    "request_data": {
      "type_action": "get_all",
      "data":{
        "id_stand_brochure": idStand
      }
    },
    "headers": {
      "Content-Type": "application/json",
      "Accept": "application/json",
      "Authorization": window.localStorage.getItem('adminToken')
    }
  }

  // Axios
  axios.post(/* process.env.URL_AMAZON_ENDPOINTS */ url + 'brochures', body, {
    headers: {
        "Content-Type": "application/json",
        "Accept": "application/json",
    },
  })
  .then((response) => {
      console.log(response)

    if(response.status == '200' && response.data.status !== 'failed' && !response.data.errorMessage){

        const dataBrochures = response.data.body.data;
     
    }
  })
  .catch(error => {
      console.error('error : ', error)
  });

}